import React, { useRef, useState, useCallback } from "react";
import { Link, useHistory } from "react-router-dom";
import { FiLogIn, FiMail, FiLock } from "react-icons/fi";
import { Form } from "@unform/web";
import { FormHandles } from "@unform/core";
import * as Yup from "yup";

import { useAuth } from "../../hooks/auth";
import Notice, { NoticeProps } from '../../components/Notice';
 
import getValidationErrors from "../../utils/getValidationErrors";

import logo from "../../assets/logo.svg";

import Input from "../../components/Input";
import Button from "../../components/Button";

import { Container, Content, AnimationContainer, Background } from "./styles";

interface SignInFormData {
  email: string;
  password: string;
}

const SignIn: React.FC = () => {
  const formRef = useRef<FormHandles>(null);

  const [loading, setLoading] = useState(false);

  const { signIn } = useAuth();

  const history = useHistory();

  const [notice, setNotice] = useState<NoticeProps>({
    display: false
  });

  const handleSubmit = useCallback(
    async (data: SignInFormData) => {
      try {
        formRef.current?.setErrors({});

        const schema = Yup.object().shape({
          email: Yup.string()
            .email("Digite um e-mail válido")
            .required("Informe seu e-mail"),
          password: Yup.string().required("Informe sua senha"),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        setLoading(true);

        await signIn({
          email: data.email,
          password: data.password,
        });

        setLoading(false);

        history.push("/tasks");
      } catch (err) {
        setLoading(false);

        if (err instanceof Yup.ValidationError) {
          const errors = getValidationErrors(err);

          formRef.current?.setErrors(errors);

          return;
        }

        setNotice({
          display: true,
          type: 'error',
          message: 'Ocorreu um erro ao fazer login, verifique suas credenciais.'
        })
      }
    },
    [signIn, history],
  );

  return (
    <Container>
      <Content>
        <AnimationContainer>
          <img src={logo} alt="Mark85" />

          <Form ref={formRef} onSubmit={handleSubmit}>
            <h1>Faça seu login</h1>

            <Notice display={notice.display} message={notice.message} type={notice.type} />

            <Input
              id="email"
              name="email"
              icon={FiMail}
              type="text"
              placeholder="E-mail"
            />
            <Input
              id="password"
              name="password"
              icon={FiLock}
              type="password"
              placeholder="Senha"
            />

            <Button id="buttonLogin" loading={loading} type="submit">
              Entrar
            </Button>

            
          </Form>

          <Link to="signup">
            <FiLogIn />
            Criar conta
          </Link>
        </AnimationContainer>
      </Content>

      <Background />
    </Container>
  );
};

export default SignIn;
