import React, { useCallback, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { FiArrowLeft, FiUser, FiMail, FiLock } from "react-icons/fi";
import { Form } from "@unform/web";
import { FormHandles } from "@unform/core";
import * as Yup from "yup";

import api from "../../services/api";

import Notice, { NoticeProps } from '../../components/Notice';

import getValidationErrors from "../../utils/getValidationErrors";

import logo from "../../assets/logo.svg";

import Input from "../../components/Input";
import Button from "../../components/Button";

import { Container, Content, AnimationContainer, Background } from "./styles";

interface SignUpFormData {
  name: string;
  email: string;
  password: string;
}

const SignUp: React.FC = () => {
  const formRef = useRef<FormHandles>(null);

  const [loading, setLoading] = useState(false);

  const [notice, setNotice] = useState<NoticeProps>({
    display: false
  });

  const handleSubmit = useCallback(
    async (data: SignUpFormData) => {
      try {
        formRef.current?.setErrors({});

        const schema = Yup.object().shape({
          name: Yup.string().required("Informe seu nome completo"),
          email: Yup.string()
            .email("Digite um e-mail válido")
            .required("Informe seu e-email"),
          password: Yup.string().min(6, "Informe uma senha com pelo menos 6 digitos"),
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        setLoading(true);

        api.post("/users", data)
          .then(response => {
            setLoading(false);

            setNotice({
              display: true,
              type: 'success',
              message: 'Boas vindas ao Mark85, o seu gerenciador de tarefas.'
            })
          }).catch(error => {
            const { message } = error.response.data

            if (message === 'Duplicated email!') {
              setNotice({
                display: true,
                type: 'error',
                message: 'Oops! Já existe uma conta com o e-mail informado.'
              })
            }
            
            setLoading(false);
          })
 
      } catch (err) {
        if (err instanceof Yup.ValidationError) {
          setLoading(false);

          const errors = getValidationErrors(err);

          formRef.current?.setErrors(errors);

          return;
        }

        setNotice({
          display: true,
          type: 'error',
          message: 'Ocorreu um erro ao fazer cadastro, verifique seu dados.'
        })
      }
    },
    [],
  );

  return (
    <Container>
      <Background />

      <Content>
        <AnimationContainer>
          <img src={logo} alt="Mark85" />

          <Form ref={formRef} onSubmit={handleSubmit}>
            <h1>Faça seu cadastro</h1>

            <Notice display={notice.display} message={notice.message} type={notice.type} />

            <Input id="name" name="name" icon={FiUser} type="text" placeholder="Nome" />
            
            <Input
              id="email"
              name="email"
              icon={FiMail}
              type="text"
              placeholder="E-mail"
            />
            <Input
              id="password"
              name="password"
              icon={FiLock}
              type="password"
              placeholder="Senha"
            />

            <Button id="buttonSignup" loading={loading} type="submit">
              Cadastrar
            </Button>
          </Form>

          <Link to="/">
            <FiArrowLeft />
            Voltar para login
          </Link>
        </AnimationContainer>
      </Content>
    </Container>
  );
};

export default SignUp;
