import React, { useCallback, useRef, useState } from "react";
import { useHistory, Link } from "react-router-dom";
import { FiArrowLeft } from "react-icons/fi";
import { Form } from "@unform/web";
import { FormHandles } from "@unform/core";
import { TagsInput } from "react-tag-input-component";
import * as Yup from "yup";

import api from "../../services/api";

import Notice, { NoticeProps } from '../../components/Notice';

import getValidationErrors from "../../utils/getValidationErrors";

import Input from "../../components/Input";
import Button from "../../components/Button";

import { Container, Content, AvatarInput } from "./styles";

import avatar from "../../assets/avatar.svg";

interface CreateFormData {
  name: string;
  email: string;
  old_password: string;
  password: string;
  password_confirmation: string;
}

const Create: React.FC = () => {
  const formRef = useRef<FormHandles>(null);

  const [loading, setLoading] = useState(false);

  const history = useHistory();

  const [tags, setTag] = useState<any[]>([]);

  const [notice, setNotice] = useState<NoticeProps>({
    display: false
  });

  const handleSubmit = useCallback(
    async (data: CreateFormData) => {
      try {
        formRef.current?.setErrors({});

        const schema = Yup.object().shape({
          name: Yup.string().required("Campo obrigatório")
        });

        await schema.validate(data, {
          abortEarly: false,
        });

        setLoading(true);

        if (tags.length > 3) {

          setLoading(false);

          return setNotice({
            display: true,
            type: 'error',
            message: 'Oops! Limite de tags atingido.'
          })
        }

        const {
          name,
        } = data;

        const formData = {
          name,
          tags
        };

        api.post("/tasks", formData)
          .then(response => {

            setLoading(false);

            history.push("/tasks");
          }).catch(error => {
            const { message } = error.response.data

            if (message === 'Duplicated task!') {
              setNotice({
                display: true,
                type: 'error',
                message: 'Oops! Tarefa duplicada.'
              })

              setLoading(false);
            } else {
              console.log(error)

              setNotice({
                display: true,
                type: 'error',
                message: 'Eita que M#$da! Deu muito ruim, veja se a API está online.'
              })
  
              setLoading(false);
            }
            
          })

      } catch (error) {
        if (error instanceof Yup.ValidationError) {
          setLoading(false);

          const errors = getValidationErrors(error);

          formRef.current?.setErrors(errors);

          return;
        }

        setLoading(false);

        setNotice({
          display: true,
          type: 'error',
          message: 'Erro ao tentar cadastrar! Tente novamente.'
        })
      }
    },
    [history, tags],
  );

  return (
    <Container>
      <header>
        <div>
          <Link to="/tasks">
            <FiArrowLeft />
          </Link>
        </div>
      </header>

      <Content>
        <Form
          ref={formRef}
          onSubmit={handleSubmit}
        >
          <AvatarInput>
            <img
              src={avatar}
              alt='Nova tarefa'
            />
          </AvatarInput>

          <h1>Cadastrar Tarefa</h1>

          <Notice display={notice.display} message={notice.message} type={notice.type} />

          <Input name="name" type="text" placeholder="Nome da tarefa" />
          
          <TagsInput
            value={tags}
            onChange={setTag}
            name="tags"
            placeHolder="Informe até 3 tags"
          />
          <em>pressione enter para adicionar a tag</em>

          <Button loading={loading} type="submit">
            Cadastrar
          </Button>
        </Form>
      </Content>
    </Container>
  );
};

export default Create;
