import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { FiPower } from "react-icons/fi";
import "react-day-picker/lib/style.css";

import { Check, Trash } from "phosphor-react";

import api from "../../services/api";

import { useAuth } from "../../hooks/auth";

import logo from "../../assets/logo.svg";
import oops from "../../assets/oops.svg"

import {
  Container,
  Header,
  HeaderContent,
  Content,
  TaskList,
  Task,
} from "./styles";

interface MonthAvailabilityItem {
  day: number;
  available: boolean;
}

interface Tasks {
  _id: string;
  name: string;
  is_done: boolean;
  tags: [string];
  user: string;
}

const Tasks: React.FC = () => {
  const { user, signOut } = useAuth();
  const [tasks, setTasks] = useState<Tasks[]>([]);

  const getTasks = async () => {
    api
      .get<Tasks[]>("/tasks")
      .then(response => {
        setTasks(response.data);
      });
  }

  useEffect(() => {
    getTasks()
  }, []);

  function handleSelectTask(task: Tasks) {
    api
      .put(`/tasks/${task._id}/${task.is_done ? 'todo' : 'done'}`)
      .then(response => {
        getTasks();
      });
  }

  function handleDeleteTask(task_id: string) {
    api
      .delete("/tasks/" + task_id)
      .then(response => {
        getTasks();
      });
  }

  return (
    <Container>
      <Header>
        <HeaderContent>
          <img src={logo} alt="Mark85" />

          <div className="right">
            <small>Olá, {user.name.split(' ')[0]}</small>
          </div>
         
          <div className="left">
            <Link to="/create">
              <button type="button" className="button-add">
                Nova tarefa
              </button>
            </Link>
            <button type="button" className="signout" onClick={signOut}>
              <FiPower color="#7f2476" /> 
            </button>
          </div>
        </HeaderContent>
      </Header>

      <Content>
        <TaskList> 
          <div>

            {tasks.length === 0 && (
              <div className="oops">
                <img src={oops} alt="Nenhuma tarefa cadastrada" />
                <p>Nenhuma tarefa cadastrada</p>
              </div>

            )}

            {tasks.map(task => (
              <Task key={task._id}>
                <div className="task-item">

                  <button
                    className={
                      task.is_done ? 'item-toggle-selected' : 'item-toggle'
                    }
                    onClick={()=> handleSelectTask(task)}
                  >
                    {task.is_done ? <Check size={24}></Check> : null}
                  </button>

                  <strong className={task.is_done ? 'task-done' : 'task-todo'}>{task.name}</strong>

                  <div className="tags">
                    {task.tags.map(tag => (
                      <span key={tag}>
                        {tag}
                      </span>
                    ))}
                  </div>

                  <button
                      className='task-remove'
                      onClick={()=> handleDeleteTask(task._id)}
                    >
                    <Trash size={24}></Trash>
                  </button>

                </div>
              </Task>
            ))}
          </div>

        </TaskList>

      </Content>
    </Container>
  );
};

export default Tasks;
